.purchase {
  .payment-submit {
    margin-top: .667rem;

    .ff-grid, .ff-grid .ff-row {
      align-items: center;
    }

    ff-btn, .ff-row > span {
      margin-right: .5rem;
    }
  }

  .payment-hint {
    margin-top: .667rem;
  }

  .purchase-dates {
    justify-content: flex-end !important;
    margin: -2px .5rem -.2rem 0;
    font-weight: normal;
    text-transform: none;

    .ff-label, .ff-date {
      min-height: 1.4rem;
      height: 1.4rem;
    }

    button.fa-calendar {
      padding-top: 2px;
      background: transparent;
    }
  }
}

purchase-audit .standout .ff-label {
  min-height: 1.8rem;
}
